import { Register, Node, ReactiveProperty } from 'io-core'
import { Camera, OrthographicCamera, PerspectiveCamera, Scene } from 'three/build/three.webgpu.js'

@Register
export class ThreeState extends Node {

  @ReactiveProperty({value: null, type: Scene})
  declare scene: Scene | null

  @ReactiveProperty({value: null, type: Camera})
  declare camera: PerspectiveCamera | OrthographicCamera | null

}